class VideoDemo extends Component {
    constructor(props) {
        super(props);

        this.state = {
            modelParams: {
                scoreThreshold: 0.6,
                flipHorizontal: false,
                maxNumBoxes: 20,
                iouThreshold: 0.5,
            },
            model: null,
            modelLoaded: false,
            videoPlayStatus: false,
            showHighlight: false,
            highlightText: "Attention needed",
            doodlecolor: "#1780DC",
            saveddoodles: [],
            selectedVideo: videoData[0],
            numHands: 0 ,
        }

        this.canvas = React.createRef();
        this.videoButton = React.createRef();
        this.imgHolder = React.createRef();
        this.video = React.createRef();

        this.media = { width: 507, height: 380 }

        this.doodlecounter = 0

        // this.chartData = {
        //     labels: ["January", "February", "March", "April", "May", "June", "July"],
        //     datasets: [
        //         {
        //             label: "My First dataset",
        //             fillColor: "rgba(220,220,220,0.2)",
        //             strokeColor: "rgba(220,220,220,1)",
        //             pointColor: "rgba(220,220,220,1)",
        //             pointStrokeColor: "#fff",
        //             pointHighlightFill: "#fff",
        //             pointHighlightStroke: "rgba(220,220,220,1)",
        //             data: []
        //         },
        //         {
        //             label: "My Second dataset",
        //             fillColor: "rgba(151,187,205,0.2)",
        //             strokeColor: "rgba(151,187,205,1)",
        //             pointColor: "rgba(151,187,205,1)",
        //             pointStrokeColor: "#fff",
        //             pointHighlightFill: "#fff",
        //             pointHighlightStroke: "rgba(151,187,205,1)",
        //             data: [28, 48, 40, 19, 86, 27, 90]
        //         }
        //     ]
        // };

        // setInterval(() => {
        //     this.chartData.labels.push(ii)
        //     this.chartData.datasets[0].data.push( Math.random() * 5)
        //     ii++
        //     console.log(this.chartData.labels)
        // }, 5000);

    }
    componentWillUnmount() {
        // this.state.model.dispose();

    }

    videoEnded(e) {
        console.log("video ended")
    }


    componentDidMount() {
        let self = this

        this.video.current.width = this.media.width
        this.canvas = document.getElementById("canvas")

        this.video.current.addEventListener("loadedmetadata", function (e) {
            var width = this.videoWidth,
                height = this.videoHeight;
            // console.log(width, height, (this.videoHeight / this.videoWidth  ) * self.media.width )
            self.canvas.height = (this.videoHeight / this.videoWidth) * self.media.width;
            self.video.current.height = (this.videoHeight / this.videoWidth) * self.media.width + 3;

        }, false);



        // this.video.current.addEventListener( "play", function (e) {
        //     console.log("video playing")
        //     self.setState({ videoPlayStatus: true })
        // }, false ); 

        // this.video.current.addEventListener( "play", function (e) {
        //     console.log("video playing")
        //     self.setState({ videoPlayStatus: true })
        // }, false ); 

        // this.video.current.addEventListener( "pause", function (e) {
        //     console.log("video paused")
        //     self.setState({ videoPlayStatus: false })
        // }, false ); 

        // this.video.current.addEventListener( "ended", function (e) {
        //     console.log("video ended")
        //     self.setState({ videoPlayStatus: false })
        // }, false ); 

        // ['pause', 'ended'].forEach(function(e) {
        //     self.video.current.addEventListener(e, self.videoEnded(), false);
        // });




        // this.video.current.src = require(`../video/golf.mp4`)

        this.canvas.width = this.media.width
        // this.canvas.height = this.media.height;

        this.canvasContext = this.canvas.getContext('2d')
        this.DoodleCenter = true

        this.canvasContext.strokeStyle = '#39ff14';
        this.canvasContext.fillStyle = "#39ff14" // "rgba(244,247,251,1)";

        handTrack.load(this.state.modelParams).then(loadedModel => {
            this.setState({ model: loadedModel })
            this.setState({ modelLoaded: true })
            // console.log("model loaded", this.state)
            // document.getElementsByClassName("handimagebox")[0].click()
            this.setState({ modelLoaded: true })
        });
        // this.setState({ modelLoaded: true })
        document.getElementsByClassName("handimagebox")[0].click()
    }

    runDetection(inputsource) {
        let self = this
        this.state.model.detect(inputsource).then(predictions => {

            // if (predictions[0]) {
            //     let midval = predictions[0].bbox[0] + (predictions[0].bbox[2] / 2)
            //     console.log('Predictions: ', inputsource.width, midval / inputsource.width);

            // }
            console.log("predictions", predictions.length)
            if (this.canvas) {
                this.runDrawPredictions(predictions)
                // console.log("FPS", model.getFPS())
                // $("#fps").text("FPS: " + model.getFPS())
                if (this.state.videoPlayStatus && inputsource) {
                    this.setState({numHands: predictions.length})
                    window.requestAnimationFrame(function () {
                        self.runDetection(inputsource)
                        
                    });

                }
            }

        });
    }

    runDrawPredictions(predictions) {
        // context  =  this.canvas.getContext('2d')
        this.canvasContext.clearRect(0, 0, this.canvas.width, this.canvas.height);

        // console.log('number of detections: ', predictions.length);
        this.canvasContext.strokeStyle = '#39ff14';
        this.canvasContext.lineWidth = 2;
        this.canvasContext.beginPath()
        for (let i = 0; i < predictions.length; i++) {

            this.canvasContext.rect(...predictions[i].bbox);
            this.canvasContext.stroke();
        }


    }


    detectImage(img) {
        this.imgHolder.current.height = 380
        this.imgHolder.current.src = img.src
        this.runDetection(this.video.current)
    }

    updateConfidence(e) {

        let modelParams = this.state.modelParams
        modelParams.scoreThreshold = e.target.value / 100
        this.setState({ modelParams })

        this.state.model.setModelParameters(this.state.modelParams)
        // let selectedBox = document.getElementsByClassName("handimageselected")[0]
        // if (!this.state.videoPlayStatus && selectedBox) {
        //     selectedBox.click()
        // }
        this.runDetection(this.video.current)

    }

    videoButtonClick(e) {
        let self = this
        if (this.state.videoPlayStatus) {
            this.setState({ videoPlayStatus: false })
            this.video.current.pause();
        } else {
            this.video.current.playbackRate = videoPlayRate;
            this.video.current.play();
            self.runDetection(self.video.current)
            this.setState({ videoPlayStatus: true })
        }
    }


    flipImageCheck(e) {
        let modelParams = this.state.modelParams
        modelParams.flipHorizontal = e.target.checked
        this.setState({ modelParams })
        this.state.model.setModelParameters(this.state.modelParams)
        console.log(this.state)
    }

    clickVideoThumbnail(e) {
        this.setState({ videoPlayStatus: false })
        let els = document.getElementsByClassName("handimagebox")
        for (var i = 0; i < els.length; i++) {
            els[i].classList.remove('handimageselected');
        }
        e.target.classList.add("handimageselected")
        this.setState({ selectedVideo: videoData[e.target.getAttribute("index")] })

        // console.log(videoData[e.target.getAttribute("index")].path);
        this.video.current.src = videoData[e.target.getAttribute("index")].path

        // if (this.state.videoPlayStatus) {
        //     let self = this
        //     let target = e.target
        //     document.getElementById("videobutton").click()
        //     setTimeout(function () {
        //         self.detectImage(target)
        //     }, 200)
        // } else {
        //     this.detectImage(e.target)
        // }


    }


    render() {

        return (
            <div className="">

                <div className="pagetitle">Gesticulator</div>
                <br />
                {this.state.modelLoaded ? null : <Loading />}

                <div className="clickable ">
                    <div className={this.state.modelLoaded ? "hidden" : "disableoverlay"}></div>
                    <div className={this.state.showHighlight ? "orangehighlight mb10" : "hidden"}> {this.state.highlightText}</div>
                    {!this.state.videoPlayStatus && (
                        <div className="bluehightlight mb10 lh10">
                            Generate a count of hands in video frames. <span className="lighttext"> </span>
                        </div>
                    )}
                    
                    {/* <div className="flex border mb10">
                        <div className="flex5">
                            <LineChart data={this.chartData} options={chartOptions} width="600" height="250"/>
                        </div>
                        <div className="flex5">
                            Chart 2
                        </div>

                    </div> */}

                    {/* <div>
                        
                        <div className="bx--form-item bx--checkbox-wrapper  flipimagecheckbox">
                            <input onClick={this.flipImageCheck.bind(this)} id="flipimagecheckbox" className="bx--checkbox" type="checkbox" value="new" name="checkbox"></input>
                            <label htmlFor="flipimagecheckbox" className="bx--checkbox-label"> Flip Image </label>
                        </div>
                    </div> */}

                    <div className="boldtext"> Hands Detected in Video: {this.state.numHands} </div>

                    <div className="flex mt10">

                        <div className="flexfull mr20" >
                            <div className="flexfull ">
                                <div id="instruction" className="lighttext mt10">Modify confidence score threshold.</div>
                                <div className="mt10 flex">
                                    <div className="slidecontainer flexfull ">
                                        <input type="range" val={this.state.modelParams.scoreThreshold * 100} onChange={this.updateConfidence.bind(this)} min="1" max="100" className="slider" id="confidencerange"></input>
                                    </div>
                                    <div className="iblock confidencethreshold ">  {this.state.modelParams.scoreThreshold} </div>
                                </div>
                            </div>
                            {/* <div className="boldtext mb10"> Click to Share Doodle </div> */}
                            <div className="bluehightlight mb10 lh10 ">
                                Detection not working as expected? Change the confidence threshold value.
                               The right threshold may depend on your <span className="boldtext">camera</span>  and <span className="boldtext">lighting</span>   conditions.
                            </div>

                            <div className="videothumbnailbox">
                                {videoData.map(item => (
                                    <div key={"itembox" + item.id} className="iblock">
                                        <img index={item.index} id={item.id} key={item.id} onClick={this.clickVideoThumbnail.bind(this)} src={item.thumbnailurl} alt="" className="handimagebox fitcover" />
                                        <div className="lighttext smalltext"> {item.title} </div>
                                    </div>
                                ))}
                            </div>

                        </div>

                        <div className="flex2 relative">

                            <div className="relative">
                                <video src={require(`../video/chess.mp4`)} ref={this.video} className="canvasbox" id="myvideo" muted></video>
                                <canvas id="canvas" className=" absolute top left clickthru"></canvas>

                                <Button className="mt10" id="videobutton" onClick={this.videoButtonClick.bind(this)} >  {this.state.videoPlayStatus ? "▩ Stop Video " : " ▶ ️ Play Video "} </Button>

                                <div className="mt10 bx--type-zeta"> {this.state.selectedVideo.sourcetitle}</div>
                                <div className="smalltext"> Source: <a rel="noopener noreferrer" target="_blank" href={this.state.selectedVideo.sourcetitle}> {this.state.selectedVideo.sourcetitle}</a>  </div>
                            </div>
                        </div>
                    </div>

                </div>


                <div className="videoboxes "></div>
                <img ref={this.imgHolder} id="imgholder" className="imgholder hidden" src="" alt=""></img>


            </div >

        );
    }
}

class Loading extends Component {
    render() {
        return (
            <div className="mb10">
                <div data-loading className="iblock bx--loading bx--loading--small">
                    <svg className="bx--loading__svg" viewBox="-75 -75 150 150">
                        <title>Loading</title>
                        <circle cx="0" cy="0" r="37.5" />
                    </svg>
                </div>
                <div className="iblock loadingtitle"> Loading Handtrack.js model ..</div>


            </div>
        )
    }
}


export default VideoDemo;