import React, { Component } from "react";
import SyntaxHighlighter from 'react-syntax-highlighter';
// import { zenburn } from 'react-syntax-highlighter/dist/esm/styles/hljs';
import { agate } from 'react-syntax-highlighter/dist/esm/styles/hljs';



let snippetLoad = `import * as handTrack from 'handtrackjs'; 

const img = document.getElementById('img'); 
const model ;

handTrack.load().then(_model => {  
    // Initial interface after model load.
    // Store model in global model variable
    model = _model
});

`

let snippetDetect = `import * as handTrack from 'handtrackjs'; 

const img = document.getElementById('img'); 

handTrack.load().then(model => {  
    // Get predictions
    model.detect(img).then(predictions => {
        console.log('Predictions: ', predictions); 
    });
});

`

let snippetParameters = `import * as handTrack from 'handtrackjs'; 

const modelParams = {
    flipHorizontal: true,   // flip e.g for video 
    imageScaleFactor: 0.7,  // reduce input image size for (maybe) gains in speed.
    maxNumBoxes: 20,        // maximum number of boxes to detect
    iouThreshold: 0.5,      // ioU threshold for non-max suppression
    scoreThreshold: 0.79,    // confidence threshold for predictions.
}
  
model.setModelParameters(modelParams)
// let params = model.getModelParameters()
`


let snippetRenderPredictions = `import * as handTrack from 'handtrackjs'; 

const img = document.getElementById('img'); 
const canvas = document.getElementById("canvas");
const ctx = canvas.getContext("2d");

handTrack.load().then(model => {  
    model.detect(img).then(preds => { 
      model.renderPredictions(preds, canvas, ctx, img);
    });
});
`

let snippetWebcamControl = `import * as handTrack from 'handtrackjs';

// Returns a promise
handTrack.startVideo(video).then(function (status) {
    if (status) {
        console.log("Video started")
    } else {
        console.log("Please enable video")
    }
});

// handTrack.stopVideo(); 
`

function Snippet(text) {
    return <SyntaxHighlighter language='javascript' style={agate}>{text}</SyntaxHighlighter>;
}

class Usage extends Component {
    render() {
        //  
        return (
            <div className="">
                <div className="pagetitle mt10">Usage Examples </div>
                <br />
                <div className="usageexamples flex">
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            Load Model
                        </div>
                        {Snippet(snippetLoad)}

                    </div>
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            Get Predictions
                        </div>
                        {Snippet(snippetDetect)}
                    </div>
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            Render Predictions
                        </div>
                        {Snippet(snippetRenderPredictions)}
                    </div>

                </div>

                <div className="usageexamples flex mt20">
                    <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            WebCam Control
                        </div>
                        {Snippet(snippetWebcamControl)}

                    </div>
                    <div className="flex6 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            Get Model Parameters
                        </div>
                        {Snippet(snippetParameters)}
                    </div>
                    {/* <div className="flex4 mr10 ">
                        <div className="bluehightlight mb10 boldtext">
                            WebCam Control
                        </div>
                        {Snippet(snippetWebcamControl)}

                    </div> */}
                </div>
                <br />
                <br />
                <br />
                <br />
                <br />

            </div>


        );
    }
}

export default Usage;